<!--
 * @Description: In User Settings Edit
 * @Author: your name
 * @Date: 2019-08-16 13:19:14
 * @LastEditTime: 2019-08-20 15:17:40
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div class="new_wrapper">
    <Header></Header>

    <el-row type="flex" justify="center" align="middle" class="new_content">
      <el-col :xs="4" :sm="6" :md="8" :lg="9" :xl="2" class="new_page">
        <div class="title">新密码</div>
        <el-form >
          <el-form-item prop="pwd">
            <el-input v-model="pwd" type="password" placeholder="密码" autofocus="autofocus"  @keyup.enter.native="confirm"></el-input>
          </el-form-item>

          <el-form-item prop="newpwd">
            <el-input v-model="newpwd" type="password" placeholder="确认密码" autofocus="autofocus" @keyup.enter.native="confirm"></el-input>
          </el-form-item>

          <el-form-item class="reset_btn">
            <el-button type="primary" :loading="btnLoad" @click.native="confirm">确认</el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
    
    <user-foot></user-foot> 
  </div>
</template>

<script>
  import Header from '@/components/header/header'
  import UserFoot from '@/components/userFoot/userFoot'
  import {vaildPassword} from '@/utils/validate'

  export default {
    props: {},
    data() {
      return {
        pwd:'',
        newpwd:'',
        email:'',
        emailCode:'',
        btnLoad:false
      }
    },
    created(){
      this.email = this.$route.query.email
      this.emailCode = this.$route.query.code
    },
    components: {
      Header,UserFoot
    },
    methods: {
      /* 确认重置密码 */
      confirm(){
        if(!vaildPassword(this.pwd)){
            this.$message.info('请输入正确格式的密码');
            return false
          }
        if(!this.newpwd){
          this.$message.info('确认密码不能为空');
          return false
        }else if(this.newpwd !== this.pwd){
          this.$message.info('两次输入密码不一致');
          return false
        }
        this.btnLoad = true
        const data = {"user":this.email, "passwd": this.pwd , "capicha":this.emailCode}
        this.$store.dispatch('reset_pwd',data).then(res => {
          if(res.data.result == 0){
            this.btnLoad = false
            this.$message.success('重置密码成功')
            this.$router.replace('/login')
          }else{
            this.btnLoad = false
          }
        }).catch(err => {
          this.btnLoad = false
        })
      }
    }
  };
</script>
<style lang="scss" scoped>
  @import '@/style/base.scss';

  .new_wrapper{
    height: 100%;
    width: 100%;
    min-width: 1280px;
    padding-top: 47px;
    .new_content{
      width: 100%;
      height: calc(100% - 180px);
      background-color:$module_bg_color;
      position: relative;
      .new_page{
        width: 80%;
        max-width: 480px;
        min-width: 400px;
        max-height: 340px;
        border-radius: 5px;
        background-color: $light_font_color;
        padding:30px 50px;
        box-sizing: border-box;
        position: absolute;
        top:50%;
        left: 50%;
        margin-top: -180px;
        margin-left: -190px;
      }
      .title{
        width: 100%;
        height: 40px;
        line-height: 40px;
        font-size: $font20;
        letter-spacing: 2px;
        color: $light_title_color;
        text-align: center;
        margin-bottom: 20px;
      }
      .reset_btn{
        margin-bottom: 15px;
        .el-button{
          width: 100%;
          background-color: $user_btn_bg_btn;
          border-radius: 3px;
          font-size: $font14;
          letter-spacing: 1px;
          margin-top: 10px;
        }
      }
    }
  }
</style>


